.errorBox {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 25px;
    font-size: 16px;
    background-color: #FFF;
    color: rgb(226, 53, 53);
    border: 2px solid rgb(226, 53, 53);
    border-radius: 15px;
    position: absolute;
    right: 10px;
    bottom: 10px;
    animation: show 1.2s ease 0s 1 forwards;
}

@keyframes show {
    0% {
        transform: translateX(100%);
        opacity: 0;
    }

    50% {
        transform: translateX(0);
        opacity: 0.7;
    }

    100% {
        opacity: 1;
    }
}